<template lang="pug">
.page-car-param
    my-nav-bar(title="车辆参数")
    my-key-value(:list="list")
</template>

<script>
export default {
    data () {
        return {
            id: '',
            list: []
        };
    },
    methods: {
        async getTax () {
            try {
                let { data } = await this.$http.post('/mobile/product/tax', { id: this.id });
                this.list = this.list.concat([{
                    label: '首付',
                    text: data.shoufu
                }, {
                    label: '月供',
                    text: data.yuegong
                }, {
                    label: '年检到期',
                    text: data.nian_date
                }, {
                    label: '交强险到期',
                    text: data.jiao_date
                }, {
                    label: '商业险到期',
                    text: data.shang_date
                }, {
                    label: '新车质保',
                    text: data.zhi_date
                }]);
                // console.log(data);
            } catch (error) {
                console.log(error);
            }
        },
        async getUnder () {
            try {
                let { data } = await this.$http.post('/mobile/product/under', { id: this.id });
                this.list = this.list.concat([{
                    label: '驱动方式',
                    text: data.dirve_type
                }, {
                    label: '转向系统',
                    text: data.turn_sys
                }, {
                    label: '前悬挂',
                    text: data.head_sys
                }, {
                    label: '后悬挂',
                    text: data.back_sys
                }]);
            } catch (error) {
                console.log(error);
            }
        },
        async getEngine () {
            try {
                let { data } = await this.$http.post('/mobile/product/engine', { id: this.id });
                this.list = this.list.concat([{
                    label: '百公里油耗(L)',
                    text: data.oil_wear
                }, {
                    label: '燃油',
                    text: data.oil
                }, {
                    label: '排量(L)',
                    text: data.displacement
                }, {
                    label: '燃油标号',
                    text: data.oil_no
                }, {
                    label: '油箱容积(L)',
                    text: data.oil_capacity
                }, {
                    label: '功率(KW)',
                    text: data.power
                }, {
                    label: '进气形式',
                    text: data.air_in
                }, {
                    label: '最高车速(km/h)',
                    text: data.speed
                }, {
                    label: '最大扭矩(N.m)',
                    text: data.nm
                }, {
                    label: '官方0-100加速(s)',
                    text: data.speed_up
                }]);
                console.log(data);
            } catch (error) {
                console.log(error);
            }
        },
        async getTyre () {
            try {
                let { data } = await this.$http.post('/mobile/product/tyre', { id: this.id });
                this.list = this.list.concat([{
                    label: '前轮制动',
                    text: data.head_drive
                }, {
                    label: '后轮制动',
                    text: data.back_drive
                }, {
                    label: '前轮规格',
                    text: data.head_std
                }, {
                    label: '后轮规格',
                    text: data.back_std
                }]);
            } catch (error) {
                console.log(error);
            }
        }
    },
    created () {
        this.id = this.$route.query.id;
        this.getTax();
        this.getUnder();
        this.getEngine();
        this.getTyre();
    }
};
</script>

<style lang="scss" scoped>
.page-car-param {
}
</style>



